  font-size: 18px;
  font-weight: 700;
  line-height: 1.5em;
}
.modal header.modal-header .modal-close-link {
  cursor: pointer;
  color: #777;
  height: 54px;
  width: 54px;
  padding: 0;
  position: absolute;
  top: 0;
  right: 0;
  text-align: center;
  border: 0;
  background-color: transparent;
  -webkit-transition: color 0.1s ease-in-out, background 0.1s ease-in-out;
  transition: color 0.1s ease-in-out, background 0.1s ease-in-out;
}
.modal header.modal-header .modal-close-link::before {
  font: normal 22px/50px dashicons !important;
  color: #666;
  display: block;
  content: '\F335';
  font-weight: 300;
}
.modal header.modal-header .modal-close-link:hover {
  background: #ddd;
  border-color: #ccc;
  color: #000;
}
.modal .modal-body {
  min-height: 100px;
  padding: 10px 40px;
  overflow-y: scroll;
}
.modal footer {
  position: absolute;
  left: 0;
  margin-left: 20px;
  z-index: 100;
  padding: 1em 1.5em;
}
.modal footer .inner {
  text-align: right;
  line-height: 23px;
}
